<template>
  <div id="backgroundImage" class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="4">
          <img class="navbar-brand-full justify-content-center" src="img/brand/logoImg.png" width="350" height="80" style="display: block;margin-left: auto;margin-right: auto;margin-bottom: 13%;">
          <b-card-group>
            <b-card no-body class="p-3">
              <b-card-body>
                <b-form @submit.stop.prevent="resetPassword">
                  <h1>{{$t('Reset_Password')}}</h1>
                  <p class="text-muted"></p>
                  <!-- <b-input-group class="mb-3">
                    <b-input-group-prepend><b-input-group-text><i class="icon-user"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="email" v-model="userName" class="form-control" :placeholder="$t('Username')" required />
                  </b-input-group> -->
                  <b-input-group class="mb-4">
                    <b-input-group-prepend><b-input-group-text><i class="icon-lock"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="password" v-model="password" class="form-control" :placeholder="$t('New_Password')" required />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend><b-input-group-text><i class="icon-lock"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="password" v-model="confirmPassword" class="form-control" :placeholder="$t('Confirm_Password')" required />
                  </b-input-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button style="background-color: #ef402b;border-color: #C33421;color: white" type="submit" :disabled="password != confirmPassword" class="px-4">{{$t('Reset')}}</b-button>
                    </b-col>
                    <!-- <b-col cols="6" class="text-right">
                      <b-button style="color: #ef402b" variant="link" to="/login" class="px-0">Login</b-button>
                    </b-col> -->
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
             

      <b-modal no-close-on-backdrop ref="conform" size="sg" hide-footer :title="$t('Password_Reset_suceesfully')" class="modal-primary">
          <template slot="modal-title">
            {{$t('Password_Reset_suceesfully')}} 
          </template>
          <div class="d-block text-center">
            <h3>{{$t('Please_login_through_Mobile_Application')}} </h3>
          </div>
          <!-- <b-button class="mt-3" block @click="closeTab()">Close</b-button> -->
  </b-modal>




          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>

import * as toutPaieService from "./ToutPaieService.js";
import VueNotifications from 'vue-notifications'

export default {
   name: 'ResetPassword',

  data: () => {
    return {
      user: {},
      /* userName: '', */
      password: '',
      confirmPassword: '',
    }
  },
   methods: {
    //  resetPassword: function(){
    //     this.user.confirm_password = this.confirmPassword;
    //     this.user.password = this.password;
    //     this.user.RememberToken = this.RememberToken;
    //     toutPaieService.resetPassword(this.user).then((res) => {
    //       if(res.status == 'Success') {
    //           VueNotifications.success({title: this.translateString('Success'), message: res.message});
    //           this.$router.push('/login');
    //       } else {
    //         VueNotifications.error({title: this.translateString('Error'), message: res.message})
    //       }
    //     });
    // },

    closeTab: function(){
      console.log("close");
      window.close();
    },
    resetPassword: function(){

     
      console.log("parameter :",this.$route.query.RememberToken);

      //  this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));

        this.user.confirm_password = this.confirmPassword;
        this.user.password = this.password;
       this.user.RememberToken = this.$route.query.RememberToken;
       this.user.From="W"

      //  var  url="user/password/change/"+this.loginUser.UserID;
      
      var  url="user/password/reset"

       toutPaieService.saveData(this.user, url).then(data => {
       // console.log('SAVEdata', form);
         
       let msg=data.data[0].message;
       let user_data=data.data[0].data.Users;

       console.log(user_data);
        if (data.status == 200 && msg=="Success") {
          
          if(user_data==null){
            VueNotifications.success({message: this.translateString("Password_Reset_Successfully") ,timeout:3000});
              this.$router.push('/login');
          }
          
          if(user_data.Role=="U"){

            this.$refs.conform.show();
        //  VueNotifications.success({message: this.translateString("Password Reset suceesfully please Login to your App") ,timeout:2500});
          }else{
            VueNotifications.success({message: this.translateString("Password_Reset_Successfully") ,timeout:3000});
              this.$router.push('/login');
          }
          
      window.localStorage.removeItem("auth_token");
      window.localStorage.removeItem("loginUser");
           
         
          //window.location.reload();
        }else{
           VueNotifications.error({message: this.translateString(msg) ,timeout:2500});
        }   
         
      });
    },

    makeToast(variant = null) {
        this.$bvToast.toast('Toast body content', {
          title: `Variant ${variant || 'default'}`,
          variant: variant,
          solid: true
        })
      },
    translateString : function(value) {
      return this.$i18n.t(value);
    },
  },
  beforeMount() {
    this.RememberToken = this.$route.params.RememberToken;
    console.log('this.RememberToken', this.RememberToken);
  }
}
</script>